<template>
	<div>
		<!-- wrap -->
		<div id="wrap">
			<!-- header -->
			<header id="sub">
				<div class="header_wrap underLineGray faqblank">
					<h1 style="padding-left:5%;">자주 찾는 질문</h1>
					<span class="faqTitle">장례박사를 이용하시는 고객님들이 가장 궁금해 하시는<br>질문과 답변을 안내합니다.</span>
				</div>
			</header>
			<!-- header -->
			<!-- content -->
			<div id="contents">
				<div class="board_faq">
					<div class="faq_list" v-for="(faq,index) in getFaqList" :key="index">
						<div class="faq_list_inner">
							<div class="q_btn">
								<p class="subject">{{faq.faqTitle}}</p>
							</div>
							<div style="width:100%; background-color:#f6f6f6; border-top:1px solid #e1e1e1;">
								<p class="a_list"><span v-html="changeTxtToHtml(faq.faqContent)"></span></p>
							</div>
						</div>
					</div>
				</div>
				<div class="paging">
					<button v-if="startPage!==1" @click="onPageChange(startPage-1)" class="prev">이전</button>
					<a :class="page===getFaqPage?'on':''" v-for="(page,index) in pages()" :key="index" @click="onPageChange(page)">{{page}}</a>
					<button v-if="getFaqPageTotal>endPage" @click="onPageChange(endPage+1)" class="next">다음</button>
				</div>
			</div>
			<!-- //content -->
			<!-- footer -->
			<Footer/>
			<!-- //footer -->
		</div>
		<!--// wrap -->
	</div>
</template>

<script>
import Footer from '@/components/common/Footer'

import myMixin from '@/mixin/mixin'

import { mapGetters } from 'vuex'

export default {
	name: 'Faq',
	mixins: [myMixin],
	components: {
		Footer
	},
	props: {},
	data() {
		return {
			faq: {
				id: null,
				faqTitle: this.$route.query.faqTitle ? this.$route.query.faqTitle : '',
				faqContent: this.$route.query.faqContent ? this.$route.query.faqContent : '',
				faqType: {
					codeId: this.$route.query.faqType ? this.$route.query.faqType : null,
				},
				page: {
					page: this.$route.query.page ? this.$route.query.page : 0,
					size: this.$route.query.size ? this.$route.query.size : 5,
					sort: this.$route.query.sort ? this.$route.query.sort : 'regDate,desc',
				}
			},
			pages: function() {
				const list = [];
				for (let i = this.startPage; i <= this.endPage; i += 1) list.push(i);
				return list
			},
		}
	},
	computed: {
		...mapGetters(['getFaqList', 'getFaqPage', 'getFaqPageTotal', 'getFaqTotalElements']),
		startPage() {
			return parseInt((this.getFaqPage - 1) / 5) * 5 + 1
		},
		endPage() {
			return (parseInt((this.getFaqPage - 1) / 5) + 1) * 5 < this.getFaqPageTotal ? (parseInt((this.getFaqPage - 1) / 5) + 1) * 5 : this.getFaqPageTotal
		}
	},
	mounted() {
		this.requestGetFaqList()
	},
	beforeDestroy() {
		this.$store.commit('FAQ_INIT')
	},
	updated() {
		var $subTopMenu = $('.faq_list .q_btn');
		$subTopMenu.unbind('click');
		$subTopMenu.on('click', function() {
			var index = $(this).index();
			if ($(this).parent().hasClass('active')) {
				$(this).parent().removeClass('active');
			} else {
				$(this).parent().addClass('active');
			}
			return false;
		});
	},
	methods: {
		requestGetFaqList() {
			const parameter = {
				faq: this.faq
			}
			this.$store.dispatch('REQUEST_FAQ_LIST', parameter)
		},
		onPageChange(val) {
			const param = {
				page: val
			}
			this.requestFaqList(param)
		},
		requestFaqList(param) {
			let currentPage = 0;
			if (param !== undefined && param !== null &&
				param.page !== undefined && param.page !== null) {
				currentPage = param.page - 1;
			}
			this.$router.push({
				path: '/faq',
				query: {
					page: currentPage,
					size: this.faq.page.size,
					sort: this.faq.page.sort
				}
			})
		},
	}
}
</script>

